@import "variables";

$scrollbar-thumb: rgb(116, 102, 134);

html {
    scrollbar-width: thin;
    scrollbar-color: $border-color $component-bg-solid;
}

::-webkit-scrollbar {
    width: 11px;
}

::-webkit-scrollbar-track {
    background: $component-bg-solid;
}

::-webkit-scrollbar-thumb {
    background-color: $border-color;
    border-radius: 6px;
    border: 3px solid $component-bg-solid;
}
