$navbar-height: 3.2rem;
$navbar-height-lg: 4.2rem;

// html {
//     overflow-y: scroll;
// }

body {
    margin-bottom: $navbar-height;
    overflow-y: scroll;
    overflow-x: hidden;
}

.navbar.fixed-bottom {
    max-height: $navbar-height;
}

@include media-breakpoint-up(lg) {
    body {
        margin-bottom: $navbar-height-lg;
    }

    .navbar.fixed-bottom {
        max-height: $navbar-height-lg;
    }
}
