$body-bg: black;
$component-bg-solid: rgb(34, 34, 51);
$component-bg: rgba($component-bg-solid, 0.6);
$body-color: #eee;
$border-color: mix(white, $component-bg, 20%);
$gradient: linear-gradient(180deg, rgba(black, 0), rgba(black, 0.25));

// $primary: #3a88fc;
$primary: #5497ff;

$link-color: $primary;
$link-decoration: none;
$link-hover-color: $link-color;
$link-hover-decoration: underline;

$input-bg: $component-bg;
$input-group-addon-bg: mix(black, $component-bg, 15%);
$input-disabled-bg: $component-bg;
$input-color: $body-color;
$input-placeholder-color: mix(black, $body-color, 30%);
$input-border-color: mix(white, $component-bg, 40%);
$form-switch-color: $input-border-color;
$form-check-input-border: 1px solid $input-border-color;
$form-select-indicator-color: $input-border-color;
// $input-disabled-border-color: null;

$navbar-dark-color: rgba($body-color, 0.7);
$navbar-dark-hover-color: $body-color;
// $nav-link-color: null !default;
// $nav-link-hover-color: null !default;
$nav-tabs-link-active-color: $body-color;
$nav-tabs-link-active-bg: $component-bg;
$nav-tabs-border-color: $input-border-color;
// $navbar-dark-disabled-color: rgba($body-color, 0.4);

// $form-label-color: mix(black, $body-color, 30%);

$card-bg: $component-bg;
$card-border-color: $border-color;
$pagination-bg: $component-bg;
$pagination-active-border-color: $border-color;
$pagination-disabled-bg: $body-bg;
$pagination-disabled-border-color: $border-color;

$alert-bg-level: 10;
$alert-color-level: -10;
