@import "variables";

@import "bootstrap/scss/bootstrap";

// cards

.card-header {
    border-bottom: none;
    background-color: rgba(0, 0, 0, 0.2);
}

.card-footer {
    border-top: none;
    padding-top: ($spacer * 0.25);
    padding-bottom: ($spacer * 0.25);
    background-color: rgba(0, 0, 0, 0.15);
}

.btn-reply {
    @include button-outline-variant($primary);
    border: none;
}

// navbar

.navbar-dark {
    background-color: $component-bg-solid;
}

// blazor form validation

.invalid {
    @extend .is-invalid;
}

.valid.modified:not([type="checkbox"]) {
    @extend .is-valid;
}

// border-lefts

@each $color, $value in $theme-colors {
    .bl-#{$color} {
        border-left-color: $value !important;
        border-left-width: 0.3rem !important;
    }
}

h4 > .badge {
    font-size: 0.5em;
    vertical-align: middle;
    margin-top: -0.1em;
}

@import "background";
// @import "lava";
@import "layout";
@import "ripple";
@import "scrollbar";
@import "floatinglabels";